import ComSearch from '@/components/search/index.vue';
import { mapState, mapActions } from 'vuex';

export default {
    name: 'Header',
    components: {
        ComSearch,
    },
    data() {
        return {};
    },
    computed: {
        ...mapState({
            user: state => state.user.user,
        }),
    },
    created() {
        this.userGet();
    },
    methods: {
        ...mapActions({
            userGet: 'user/userGet',
        }),
    },
};



